/* LIGHTBOX */

#lightbox {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 105%;
    background-color: rgba(0,0,0,0.85);
    display: none;
    top: 49%;
    transform: translateY(-50%);
    &.active {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .img-wrapper {
        position: relative;
        .close-btn {
            position: absolute;
            top: -10px;
            right: -100px;
            padding: 10px;
            img {
                width: 80px;
            }
        }
        .prev-btn {
            position: absolute;
            top: 50%;
            left: -200px;
            padding: 30px;
            transform: translateY(-50%);
            img {
                width: 120px;
            }
        }
        .next-btn {
            position: absolute;
            top: 50%;
            right: -200px;
            padding: 30px;
            transform: translateY(-50%);
            img {
                width: 120px;
            }
        }
        .img-title {
            p {
            color: white;
            font-size: 36px;
            text-align: center;
            }
        }
    }
    .img-wrapper > img {
        padding: 100px 0 10px 0;
        max-width: 50vw;
        max-height: 85vh;
        
    }
}


  .prev-btn img:hover,
  .next-btn img:hover,
  .close-btn:hover {
    cursor: pointer;
  }
  
  .prev-btn.disabled,
  .next-btn.disabled {
    opacity: 0.1;
  }

